.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
  box-shadow: 0 2px 4px 0 #25272c;
}

.logo {
  margin-left: 20px;
  color: blanchedalmond;
}

.nav {
  /* background-color: rgb(203, 180, 146); */
  display: flex;
  margin: 0 10px 0 10px;
}

.navlist {
  /* background-color: rgb(96, 137, 167); */
  list-style: none;
  display: flex;
  justify-content: center;
}

.navlist>li {
  padding: 10px;
}